@import "mixins";


.problem {
    width: 100%;
    padding: 128px 0;
    @include mobile {
        padding: 40px 0;
     }
    // .problem__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .problem__top

    &__top {
        margin: 0 0 88px 0;
        @include flex-all-sb;
        align-items: flex-start;
        @include mobile {
            flex-direction: column;
            margin: 0 0 48px 0;
        }
    }

    // .problem__title

    &__title {
        max-width: 350px;
        margin: 0 32px 0 0;
        @include mobile {
           margin: 0 0 30px 0;
           max-width: 500px
        }
    }

    // .problem__items

    &__items {
    }

    // .problem__item

    &__item {
        @include flex-all-sb;
        align-items: flex-start;
        &:first-child {
            margin: 0 0 60px 0;
            @include mobile {
                margin: 0 0 40px 0;
             }
        }
    }

    // .problem__illustration

    &__illustration {
        width: 56px;
        height: 56px;
        margin: 0 24px 0 0;
        @include mobile2 {
            margin: 0 14px 0 0;
            width: 46px;
            height: 46px;
        }
    }

    // .problem__text

    &__text {
        font-size: 22px;
        font-weight: 300;
        max-width: 615px;
        line-height: 160%;
        @include mobile {
            font-size: 18px;
        }
    }

    // .problem__bottom

    &__bottom {
        width: 100%;
        min-height: 292px;
        border: 3px solid $blueColor;
        padding: 0 90px;
        @include flex-all-center;
        @include tablet2 {
            padding: 0 35px;
        }
        @include mobile {
            padding: 0 20px;
        }
    }
 
    // .problem__block

    &__block {
    }

    // .problem__desctiption

    &__desctiption {
        font-weight: 300;
        font-size: 28px;
        margin: 0 0 35px 0;
        @include mobile {
        font-size: 24px;
            margin: 0 0 25px 0;
        }
    }

    // .problem__button

    &__button {
        width: 306px;
        @include mobile2 {
            width: 230px;
        }
    }
}

