@import "mixins";


.useful {
    width: 100%;
    background-color: $lightColor;
    padding:88px 0 124px 0;
    @include mobile {
        padding:44px 0 54px 0;
    }

    // .useful__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .useful__title

    &__title {
        margin: 0 0 50px 0;
    }

    // .useful__row

    &__row {
        @include flex-all-sb;
        @include tablet2 {
            flex-direction: column;
        }
    }

    // .useful__item
 
    &__item {
        @include tablet2 {
            width: 100%;
            margin: 0 0 30px 0;
            height: 300px;
        }
        @include mobile2 {
            min-height: 320px;
            height: auto;
            padding: 16px 24px 16px 24px;
        }
        transition: .3s all ease 0s;
        cursor: pointer;
        width: 350px;
        height: 365px;
        box-shadow: 0px 8px 24px rgba(125, 125, 165, 0.16), 0px 1px 4px rgba(210, 220, 255, 0.6);
        border-radius: 2px;
        background-color: #fff;
        padding: 16px 24px 0 24px;
        &:hover {
            box-shadow: 0px 8px 24px rgba(125, 125, 165, 0.3), 0px 1px 4px rgba(210, 220, 255, 0.7);
        }
        margin: 0 16px 0 0;
        &:last-child {
            margin: 0;
        }
    }

    // .useful__illustration

    &__illustration {
        width: 80px;
        height: 80px;
        margin: 0 0 24px 0; 
      
    }

    // .useful__caption

    &__caption {
        font-weight: 500;
        font-size: 18px;
        margin: 0 0 35px 0;
        @include mobile {
            margin: 0 0 20px 0;
        }
    }

    // .useful__text

    &__text {
        font-weight: 300;
        font-size: 18px;
    }
}
