@import "mixins";


.screen {
    width: 100%;
    padding: 128px 0;
    @include mobile {
        padding: 54px 0;
    }
    // .screen__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        align-items: flex-start;
        @include tablet2 {
            flex-direction: column;
        }
    }

    // .screen__image

    &__image {
        width: 513px;
        @include tablet2 {
           width: 100%;
           margin: 0 0 30px 0;
        }
    }

    // .screen__content

    &__content {
        max-width: 531px;
        @include tablet2 {
           max-width: 700px;
         }
    }

    // .screen__title

    &__title {
        margin: 0 0 45px 0;
        @include mobile {
            margin: 0 0 30px 0;
        }
    }

    &__text,  &__desctiption {
        font-size: 18px;
        font-weight: 300;
    }
    // .screen__text

    &__text {
        margin: 0 0 35px 0;
        @include mobile {
            margin: 0 0 25px 0;
        }
    }

    // .screen__desctiption

    &__desctiption {
        span {
            font-weight: 500;
        }
    }
}
