@import "mixins";


.author {
    width: 100%;
    padding: 140px 0 130px 0;
    @include mobile {
        padding: 70px 0 65px 0;
    }
    // .author__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .author__title

    &__title {
         margin: 0 0 45px 0;
         @include mobile {
            margin: 0 0 25px 0;
        }
    }

    // .author__content

    &__content {
        width: 100%;
        @include flex-all-sb;
        align-items: flex-start;
        @include mobile {
            flex-direction: column;
        }
    }

    // .author__image

    &__image {
        width: 350px;
        height: 100%;
        margin: 0 35px 0 0;
        @include mobile {
            width: 100%;
            margin: 0;
        }
    }

    // .author__column

    &__column {
        max-width: 665px;
        margin: 15px 0 0 0;
    }

    // .author__item

    &__item {
        width: 100%;
        font-weight: 300;
        font-size: 16px;
        line-height: 160%;
        border-bottom: 1px solid #D6D8DC;
        padding-bottom: 16px;
        margin: 0 0 20px 0;
        &:last-child {
            border: none;
        }
        a {
            color: #2486DC;
            img {
                width: 20px;
                height: 20px;
                transform: translateY(5px);
                margin: 0 0 0 3px;
            }
        }
    }
}
