@import "mixins";


.program {
    width: 100%;
    background-color: $lightColor; 
    padding: 85px 0 130px 0;
    @include mobile {
        padding: 42px 0 65px 0;
    }
    // .program__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .program__title

    &__title {
        margin: 0 0 45px 0;
        @include mobile {
            margin: 0 0 35px 0;
        }
    }

    // .program__content

    &__content {
        width: 100%;
    }

    // .program__item

    &__item {
        width: 100%;
        @include flex-all-sb;
        align-items: flex-start;
        margin: 0 0 90px 0;
        &:last-child {
            margin: 0;
        }
        @include mobile {
            flex-direction: column;
            margin: 0 0 40px 0;
        }
    }

    // .program__left

    &__left {
        max-width: 520px;
        @include flex-all-sb;
        align-items: flex-start;
        margin: 0 20px 0 0;
        @include mobile {
            margin: 0;
            max-width: 100%;
        }
    }

    // .program__number

    &__number {
        width: 56px;
        height: 56px;
        margin: 0 30px 0 0;
        @include mobile2 {
            width: 46px;
            height: 46px;
            margin: 0 15px 0 0;
        }
    }

    // .program__texts

    &__texts {
        @include mobile {
           margin: 0 0 20px 0;
         }
    }

    // .program__caption

    &__caption {
        font-weight: 500;
        font-size: 22px;
        margin: 0 0 30px 0; 
        @include mobile {
            font-size: 18px;
            margin: 0 0 10px 0; 
        }
    }

    // .program__text

    &__text {
        font-weight: 300;
        margin: 0 0 30px 0;
        line-height: 160%;
        @include mobile {
            margin: 0 0 15px 0; 
        }
    }

    // .program__image

    &__image {
        width: 530px;
        @include tablet2 {
            width: 350px;
        }
        @include mobile {
           width: 100%;
           padding-left: 86px;
        }
        @include mobile2 {
            padding-left: 61px;
        }
    }

    // .program__ul

    &__ul {
        li {
            font-weight: 300;
            margin: 0 0 20px 0;
            line-height: 160%;
            @include mobile {
                margin: 0 0 10px 0;
             }
        }
    }
}
