@import "mixins";


.end {
    width: 100%;
    padding: 88px 0 128px 0;
    background-color: $lightColor;
    @include mobile {
        padding: 48px 0 54px 0;
    }
    // .end__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        align-items: flex-start;
        @include tablet2 {
            flex-direction: column;
        }
    }

    // .end__title
 
    &__title {
        margin: 0 40px 0 0;
        @include tablet2 {
            margin: 0 0 40px  0;
        }
    }

    // .end__items

    &__items {
        max-width: 690px;
    }

    // .end__item

    &__item {
        width: 100%;
        @include flex-all-sb;
        align-items: flex-start;
        margin: 0 0 48px 0;
    }

    // .end__check

    &__check {
        width: 48px;    
        height: 32px;
        margin: 0 24px 0 0;
        @include mobile2 {
            margin: 0 16px 0 0;
        }
    }

    // .end__text

    &__text {
        font-weight: 300;
        font-size: 22px;
        line-height: 132%;
        @include mobile2 {
            font-size: 18px;
        }
    }
}
