@import "mixins";


.registration {
    width: 100%;
    padding: 130px 0 0 0;
    @include mobile {
        padding: 65px 0 0 0;
    }
    // .registration__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        align-items: flex-start;
        @include mobile {
            flex-direction: column;
        }
    }

    // .registration__info

    &__info {
         margin: 0 30px 0 0;
         max-width: 440px;
         @include mobile {
           margin: 0 0 30px 0;
        }
    }


    // .registration__title
 
    &__title {
       margin: 0 0 45px 0;
       @include mobile {
        margin: 0 0 35px 0;
    }
    }

    // .registration__text
 
    &__text {
        font-weight: 300;
        font-size: 22px;
        line-height: 160%;
    }

    // .registration__block

    &__block {
        max-height: 622px;
        min-height: 522px;
        background-color: $beigeColor;
        border-radius: 3px;
        border-top: 3px solid $blueColor;
        padding: 45px 47px 20px 47px;
        @include tablet2 {
            padding: 25px 27px 20px 27px;
        }
        @include mobile {
            max-width: 100%;
            min-height: auto;
            width: 100%;
            padding: 15px 17px 30px 17px;
        }
    }

    // .registration__form

    &__form {
        width: 100%;
        label {
            font-size: 18px;
        }
    }

    // .registration__input

    &__input {
        margin: 16px 0 16px 0;
         outline: none;
         border: 1px solid #D6D8DC;
         border-radius: 2px;
         background-color: #fff;
         width: 100%;
         height: 58px;
         padding: 0 15px;
        &:nth-child(3) {
            margin: 0 0 16px 0;
            @include mobile {
                margin: 0 0 10px 0;
            }
        }
        @include mobile {
            margin: 10px 0 10px 0;
        }
    }
 
    // .registration__confidency

    &__confidency {
        width: 100%;
        margin: 0 0 40px 0;
        font-size: 12px;
        font-weight: 300;
        @include mobile {
            margin: 0 0 20px 0;
        }
        a {
            color: #2486DC;
        }
    }

    // .registration__button

    &__button {
        width: 100%;
    }

    // .registration__item

    &__item {
        &:first-child {
            margin: 0 0 16px 0;
        }
    }
}
