@import "mixins";


.system {
    width: 100%; 
    padding: 130px 0 270px 0;
    @include mobile {
        padding: 65px 0 110px 0;
    }
    // .system__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .system__title

    &__title {
        margin: 0 0 45px 0;
        @include mobile {
            margin: 0 0 35px 0;
        }
    }

    // .system__content

    &__content {
        width: 100%;
        @include flex-all-sb;
        align-items: flex-start;
        @include tablet2 {
            flex-direction: column;
        }
    }

    // .system__left

    &__left {
        max-width: 575px;
        margin: 0 30px 0 0;
        @include tablet2 {
            margin: 0 0 50px 0;
            max-width: 100%;
            width: 100%;
        }
    }

    // .system__item

    &__item {
        width: 100%;
        font-weight: 300;
        padding-bottom: 25px;
        margin: 0 0 20px 0;
        border-bottom: 1px solid #D6D8DC;
        &:last-child {
            margin: 0;
        }
    }

    // .system__right

    &__right {
        max-width: 440px;
        @include tablet2 {
           max-width: 700px;
        }
    }

    // .system__text

    &__text {
        font-weight: 300;
        margin: 0 0 30px 0;
        line-height: 160%;
        &:last-child {
            border-bottom: 1px solid #D6D8DC;
            margin: 0;
            padding-bottom: 12px;
            @include tablet2 {
               border: none;
               padding: 0;
            }
        }
        a {
            color: #2486DC;
            img {
                width: 20px;
                height: 20px;
                margin: 0 0 0 3px;
            }
        }
    }
}
